<template>
	<header>
		<h1>
            <a href="#" class="logo">
                <img alt="Vue logo" src="../../assets/logo.png" width="80">
            </a>
        </h1>

		<div class="menu_container">
			<ul class="menu">
                <li @mouseenter="onMouseEnter" @mouseleave="onMouseLeave">
                    <router-link to="/intro">소개</router-link>
                </li>
				<li @mouseenter="onMouseEnter" @mouseleave="onMouseLeave">
                    <router-link to="/portfolio1">포트폴리오 1</router-link>
                </li>
				<li @mouseenter="onMouseEnter" @mouseleave="onMouseLeave">
                    <router-link to="/portfolio2">포트폴리오 2</router-link>
                </li>
				<li @mouseenter="onMouseEnter" @mouseleave="onMouseLeave">
                    <router-link to="/portfolio3">포트폴리오 3</router-link>
                </li>
			</ul>
		</div>
	</header>
</template>

<script>
export default {
    methods: {
        onMouseEnter: function (e) {
            e.target.classList.add('bounce');
        },
        onMouseLeave: function (e) {
            e.target.classList.remove('bounce');
        }
    }
}
</script>

<style scoped>
header {
    width: 100%;
    text-align: center;
    position: relative; 
    height: 100px; 
    border-bottom: 1px solid #35495e;
}
header h1 {
    position: absolute;
    top: -20px; 
    left: 60px;
}
.menu_container {
    float: right;
    height: 100%;
    width: 800px;
}
.menu_container ul li {
    list-style: none;
    float: left;
    line-height: 50px;
    text-align: center;
    vertical-align: middle;
    padding-left: 20px;
}
a {
    text-decoration: none;
    color: #333;
}
@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {transform: translateY(0);}
    40% {transform: translateY(-15px);}
    60% {transform: translateY(-10px);}
} 
.bounce {
    -webkit-animation-duration: 0.6s;
    animation-duration: 0.6s;
    -webkit-animation-name: bounce;
    animation-name: bounce;
}
</style>