import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import HelloWorld from './components/HelloWorld.vue'
import Portfolio1 from './components/Portfolio1.vue'
import Portfolio2 from './components/Portfolio2.vue'
import Portfolio3 from './components/Portfolio3.vue'

Vue.config.productionTip = false

Vue.use(VueRouter);

const router = new VueRouter({
  mode: "history",
  routes: [
    { path: "/", component: HelloWorld },
    { path: "/intro", component: HelloWorld },
    { path: "/portfolio1", component: Portfolio1 },
    { path: "/portfolio2", component: Portfolio2 },
    { path: "/portfolio3", component: Portfolio3 },
  ],
})

new Vue({
  render: h => h(App),
  router
}).$mount('#app')
